import React, { useEffect } from 'react'
import { load } from '@2gis/mapgl';
import './App.css'

const controlContent = `
  <div class="buttonRoot" id="find-me">
      <button class="button">
          <svg
              xmlns="http://www.w3.org/2000/svg"
              width="32"
              height="32"
              viewBox="0 0 32 32"
          >
              <path
                  fill="currentColor"
                  d="M17.89 26.27l-2.7-9.46-9.46-2.7 18.92-6.76zm-5.62-12.38l4.54 1.3 1.3 4.54 3.24-9.08z"
              />
          </svg>
      </button>
  </div>
  <p id="status"></p>
`;

function App({coords, date}) {
  useEffect(() => {
    let map;
    let circle;
    let control;
    let status;

    const addNavIcon = (map, mapglAPI) => {
      control = new mapglAPI.Control(map, controlContent, {
        position: 'topLeft',
      });
    }

    const addCircleIcon = (map, mapglAPI, center) => {
      circle = new mapglAPI.CircleMarker(map, {
        coordinates: center,
        radius: 14,
        color: '#0088ff',
        strokeWidth: 4,
        strokeColor: '#ffffff',
        stroke2Width: 6,
        stroke2Color: '#0088ff55',
      });
    }

    const newMarker = (mapgl) => {
      const marker = new mapgl.Marker(map, {
        coordinates: coords,
        icon: 'https://docs.2gis.com/img/mapgl/marker.svg',
      })
      marker.on('click', (e) => {
        new mapgl.HtmlMarker(map, {
          coordinates: coords,
          html: `<div style='position: absolute;transform: translate(-50%, 50%);display: flex;flex-direction: column;min-width:215px;opacity:0.8;'>
                    <div style='padding: 9px;border-radius: 11px;background: #FFC0CB;box-shadow: 0 1px 2px 0 rgba(38, 38, 38, 0.2);'>
                      <p style='line-height:18px;font-size:17px;margin:0;'>Вы зачекинились здесь</p>
                      <p style='line-height:18px;font-size:17px;margin:0;'>Дата чекина: ${date}</p>
                    </div>
                    <div style='position: absolute;transform: translate(-5%, -100%);width: 0;height: 0;align-self: center;border-left: 10px solid transparent;border-right: 10px solid transparent;border-bottom: 10px solid #FFC0CB;'/>
                </div>`,
        });
      });
    }

    const searchGeoposition = (mapglAPI) => {
      status = control.getContainer().querySelector('#status');

      function success(pos) {
        const center = [pos.coords.longitude, pos.coords.latitude];

        status.textContent = '';
        if (circle) {
          circle.destroy();
        }

        addCircleIcon(map, mapglAPI, center)
        map.setCenter(center);
        map.setZoom(16);
      }

      function error() {
        status.textContent = 'Не удалось определить ваше местоположение';
      }

      function geoFindMe() {
        if (!navigator.geolocation) {
            status.textContent = 'Геолокация не поддерживается вашим браузером';
        } else {
          status.textContent = 'Определение местоположения…';
          navigator.geolocation.getCurrentPosition(success, error);
        }
      }

      control
        .getContainer()
        .querySelector('#find-me')
        .addEventListener('click', geoFindMe);
    }

    load().then((mapglAPI) => {
      if (!navigator.geolocation) {
        map = new mapglAPI.Map('map-container', {
          center: [55.31878, 25.25584],
          zoom: 13,
          key: '042b5b75-f847-4f2a-b695-b5f58adc9dfd'
        });

        addNavIcon(map, mapglAPI)
        searchGeoposition(mapglAPI)
        newMarker(mapglAPI)
      } else {
        navigator.geolocation.getCurrentPosition(
          function(position) {
            const center = [position.coords.longitude, position.coords.latitude]
            map = new mapglAPI.Map('map-container', {
              center: center,
              zoom: 13,
              key: '042b5b75-f847-4f2a-b695-b5f58adc9dfd'
            });

            addCircleIcon(map, mapglAPI, center)
            map.setCenter(center);
            map.setZoom(16);

            addNavIcon(map, mapglAPI)
            searchGeoposition(mapglAPI)
            newMarker(mapglAPI)
          },

          function(err) {
            map = new mapglAPI.Map('map-container', {
              center: [55.31878, 25.25584],
              zoom: 13,
              key: '042b5b75-f847-4f2a-b695-b5f58adc9dfd'
            });

            addNavIcon(map, mapglAPI)
            searchGeoposition(mapglAPI)
            newMarker(mapglAPI)
          }
        )
      }

      
    });
    // Удаляем карту при размонтировании компонента
    return () => map && map.destroy();
  }, [date, coords]);

  return (
    <>
      <div style={{ width: '100vw', height: '100vh' }}>
        <div id="map-container" style={{ width: '100%', height: '100%' }} />
      </div>
    </>
  );
}

export default App;
